.custom-toggle {
  position: relative;
  font-size: 18px;
  line-height: 27px;
  font-weight: 300;

  display: flex;
  width: 100%;
  padding: 0;

  &.is-invalid {
    label span {
      color: #ff1553;
    }
  }

  .input-message {
    position: absolute;
    right: 0;
    bottom: -5px;

    &.is-invalid {
      color: #ff0000;
    }

    &.is-valid {
      color: #15ab07;
    }
  }
}

.custom-toggle label {
  position: relative;

  padding: 5.5px 0 5.5px 30px;

  cursor: pointer;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 12px;
  color: $color-default-black;
  opacity: 0.8;
}

.custom-toggle input {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;

  clip: rect(0 0 0 0);
}

.custom-toggle input:checked + .custom-toggle__icon {
  border: 1.5px solid #2c39f2;

  &::before {
    background: url("../../img/svg/tick.svg") no-repeat center;
    background-size: 10px 10px;
    background-color: $color-cerulean-blue;
  }
}

.custom-toggle input:checked + .custom-toggle__icon--dark {
  border: 1px solid $color-default-white;
  border-radius: 2px;

  &::before {
    background: url("../../img/svg/check.svg") no-repeat center;
    background-size: 10px 10px;
    background-color: $color-default-white;
  }


}

.custom-toggle--checkbox-form {
  max-width: 350px;
  padding: 0;
}

.custom-toggle--checkbox-form label {
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 12px;
  opacity: 0.9;
  color: $color-default-white;
  padding: 5px 0 5px 30px;

  @include vp-767 {
    line-height: 14px;
    padding: 0 0 0 50px;
  }
}

.custom-toggle__icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  padding: 6px 4px;

  border: 1px solid $color-cerulean-blue;
  border-radius: 2px;

  transition: border 0.3s ease;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;

    width: 20px;
    height: 20px;

    background-image: url("../../img/sprite.svg#tick");

    transform: translate(-50%, -50%);

    transition: background-color 0.3s ease;
  }
}

.custom-toggle__icon--dark {
  border: 1px solid $color-default-white;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;

    width: 20px;
    height: 20px;

    background-image: url("../../img/sprite.svg#tick-dark-bg");

    transform: translate(-50%, -50%);

    transition: background-color 0.3s ease;
  }
}
